import { Component, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Tabs } from 'ionic-angular';
import { File } from '@ionic-native/file';

import { Observable } from 'rxjs/Rx';
import { LoopBackConfig } from '../../../shared/sdk/index';


import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { ImageServiceProvider } from '../../providers/image-service/image-service';
import { ImageFileServiceProvider } from '../../providers/image-file-service/image-file-service';
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { UtilServiceProvider } from '../../providers/util-service/util-service';


/**
 * Generated class for the ImagesPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-images',
  templateUrl: 'images.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ImagesPage {
  username: any;
  t: Tabs = this.navCtrl.parent;
  lpda_images;
  pda_images;
  images;
  images_present: boolean;
  images_loaded: boolean;
  subscription;
  result = null;
  driverId: number;
  loginFlag = this.pdaParams.isDrvLoggedOn();

  constructor(public navCtrl: NavController, public navParams: NavParams, private file: File,
    private configService: ConfigServiceProvider, private pdaParams: PdaparamsServiceProvider,
    private imageService: ImageServiceProvider, private siteConfig: SiteconfigServiceProvider,
    private sharedService: SharedServiceProvider,
    private imageFileService: ImageFileServiceProvider, private alertCtrl: AlertController,
    private utilService: UtilServiceProvider, private cd: ChangeDetectorRef) {
    //console.log("ImagesPage loading...");
    this.username = navParams.data;
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
    this.driverId = this.pdaParams.getDriverId();
    //Monitor Image Refreshes.
    this.sharedService.imageRefresh.subscribe(res => {
      this.getImages();
    });
  }//End of the constructor.


  ionViewWillEnter() {
    this.images_loaded = false;
    this.images_present = false;
    if (this.sharedService.testing) {
      this.lpda_images = 'Y';
      this.pda_images = 'Y';
    } else {
      this.lpda_images = this.pdaParams.pda_images ? 'Y' : 'N';
      this.pda_images = this.siteConfig.getSiteConfigYN('PDA_IMAGES');
    }
    if (this.pda_images != 'Y' && this.lpda_images != 'Y') {
      let alert = this.alertCtrl.create({
        title: 'Module not installed',
        subTitle: 'This module is not installed.',
        buttons: [{
          text: 'OK',
          handler: () => {
            const navTransition = alert.dismiss();
            navTransition.then(() => {
              this.t.select(0);
            });
            return false;
          }
        }]
      });
      alert.present();
    } else {
      this.getImages();
    }
  }// ionViewWillEnter() ends here


  getImages = () => {
    this.imageService.getImageList().subscribe((images) => {
      var len = images.length;
      if (len > 0)
        this.images_present = true;
      //For each of the images retrieved from file system grab any notes and update the uploaded flag.
      for (var i = 0; i < len; i++) {
        var notes = this.imageFileService.getImageProperty(images[i].name, 'notes');
        if (notes != null) {
          images[i].notes = notes;
        }
        images[i].uploaded = this.imageFileService.getImageProperty(images[i].name, 'uploaded');
        images[i].readyForUpload = this.imageFileService.getImageProperty(images[i].name, 'readyForUpload');
      }
      this.images = images;
      this.images_loaded = true;
      this.cd.detectChanges();
    });
  }//getImages() ends here

  //Riyaz: See https://github.com/justinwoodcock/Loopback-Upload
  uploadImage = (image) => {
    this.imageService.uploadImage(image, (result) => {
      this.images_loaded = false;
      this.getImages();
    });
  }

  deleteImage = (imageName) => {
    this.imageService.deleteSingleImage(imageName).subscribe((success) => {
      this.getImages();		// reload images into scope     
    }, function (err) {
      this.log.error(err);
    });
  }

  deleteObsoleteImages = () => {
    this.imageService.deleteObsoleteImages().subscribe((result) => {
      this.result = result;
      this.navCtrl.setRoot(this.navCtrl.getActive().component);
    }, (err) => {
      //this.log.error(err);
    });
  }
}


