import { Injectable } from '@angular/core';

import 'rxjs/add/operator/map';
import { timestamp } from 'rxjs/operator/timestamp';
import { DespatchToDriverMessagesApi } from '../../../shared/sdk';

/*
  Generated class for the DriverMessageServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/

export interface driverMsgInt {
  drvmDriverId: any;
  drvmDate: any;
  drvmTimestamp: any;
  drvmMessage: string;
  drvmSender: any;
  drvmRecipient: any;
  drvmSpare: any;

}

@Injectable()
export class DriverMessageServiceProvider {

  constructor(private despatchToDriverMessages: DespatchToDriverMessagesApi) {
    //console.log('Hello DriverMessageServiceProvider Provider');
  }

  driverMsg: driverMsgInt;

  getMessages(driverId) {
    // TODO - limit shoud be a config or pda parameter
    var filter = {
      "where": { "drvmDriverId": driverId },
      "order": 'drvmSeqid DESC',
      "limit": 20
    };

    return (this.despatchToDriverMessages.find(filter).subscribe());
  };

  addMessage(driverId, message, cb) {

    //var now = new Date();

    //console.log("driverId, message, cb" + driverId + "," + message + "," + cb);
    var now = new Date().toISOString();

    this.driverMsg = {
      drvmDriverId: driverId,
      drvmDate: now,
      drvmTimestamp: now,
      drvmMessage: message,		// TODO - should make sure <:512 chars
      drvmSender: driverId,
      drvmRecipient: "DESPATCH",
      drvmSpare: ''
    };	// for now will always be DESPATCH until we maybe have a reply concept

    this.despatchToDriverMessages.create(this.driverMsg).subscribe();
    //this.despatchToDriverMessages.upsert(this.driverMsg).subscribe();
  };


}



