import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

import { LoopBackConfig } from '../../../shared/sdk/index';

import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { ImageFileServiceProvider } from '../../providers/image-file-service/image-file-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

/**
 * Generated class for the ImageTextPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-image-text',
  templateUrl: 'image-text.html',
})
export class ImageTextPage {
  photoMessage;
  loginFlag = this.pdaParams.isDrvLoggedOn();
  newFileName: string = this.navParams.get('newFileName');

  constructor(public navCtrl: NavController, private imageFileService: ImageFileServiceProvider,
    public navParams: NavParams, public viewCtrl: ViewController,
    private configService: ConfigServiceProvider, private pdaParams: PdaparamsServiceProvider,
    private sharedService: SharedServiceProvider) {
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
  }

  save = () => {
    var notes = this.photoMessage;
    this.imageFileService.setImageProperty(this.newFileName, 'notes', notes);
    // close after saving
    this.closeModal();
  };

  // function to close the modal
  closeModal = () => {
    if (this.newFileName)
      this.imageFileService.setImageProperty(this.newFileName, 'readyForUpload', 'true');
    this.clear();
    this.viewCtrl.dismiss();
  };

  clear = () => {
    this.photoMessage = "";
  };


}



