import { Injectable } from '@angular/core';
import { Device } from '@ionic-native/device';
import { Platform } from 'ionic-angular';
import { Insomnia } from '@ionic-native/insomnia';

import 'rxjs/add/operator/map';

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { BackgroundGeolocationServiceProvider } from '../../providers/background-geolocation-service/background-geolocation-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

/*
  Generated class for the PlatformReadyServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/


@Injectable()
export class PlatformReadyServiceProvider {

  logOb;
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'PlatformReadyServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  isready: boolean = false;

  constructor(private device: Device, private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider, public platform: Platform,
    private insomnia: Insomnia, private backgroundGeolocationService: BackgroundGeolocationServiceProvider,
    private sharedService: SharedServiceProvider) {
    this.log.info('Hello PlatformReadyServiceProvider Provider');

    this.platform.ready().then((readySource) => {
      this.log.info('Platform ready from :', readySource);
      // Platform now ready, execute any required native code

      var model = this.device.model;
      var platform = this.device.platform;
      var version = this.device.version;

      /*  if (platform == 'android') {
         StatusBar.backgroundColorByHexString("#333");
       } */

      this.isready = true;
      // Keep screen on for our app
      this.insomnia.keepAwake();

      //this.log.info("Device Model:" + model + ", platform:" + platform + ", OS ver:" + version);

      if (model == 'CipherLab RS30') {
        this.log.debug("Device is a CipherLab RS30 " + model);
      }
      else {
        this.log.debug("Device is NOT !!!! a CipherLab RS30 HEAD scanning NOT Available " + model);
      }

      this.platform.pause.subscribe((e) => {
        this.log.debug("platform.pause: About to call BGGS.start() ");
        this.backgroundGeolocationService.start();

        //$rootScope.$broadcast('PAUSE');
        this.sharedService.PAUSE.next(true);
      });

      this.platform.resume.subscribe((e) => {
        this.log.debug("platform.resume: About to broadcast: RESUME event");
        this.backgroundGeolocationService.stop();

        //$rootScope.$broadcast('RESUME');
        this.sharedService.RESUME.next(true);
      });

    });
  }//constructor ends;


  isPlatformReady() {
    return this.isready;
  }


  //document.addEventListener("deviceready", onDeviceReady, false);

  //function onDeviceReady() {
  // Now safe to use device APIs

  //log.debug('onDeviceReady');

  /*		// LT - I don't think we should clear cache/localstorage here on startup as e.g.
      // if a driver completes jobs when offline and starts up next day we need those
      // to sync rather than get a new set of data from the server
  
      // clear webview cache
      var success = function(status) {
        alert('Message: ' + status);
      }
  
      var error = function(status) {
        alert('Error: ' + status);
      }
      localStorage.removeItem('osc-local-db');
      window.cache.clear( success, error );
  */
  //alert(' CordovaIsready = ' + isready);

  // LT - TODO - set up log writing - gave an error on android device
  /*
      window.resolveLocalFileSystemURL(cordova.file.dataDirectory, function(dir) {
        this.log.info("got main dir",dir);
        dir.getFile("log.txt", {create:true}, function(file) {
          this.log.info("got the file", file);
          logOb = file;
          writeLog("App started");			
        });
      });
  */

  // https://github.com/driftyco/ng-cordova/blob/master/src/plugins/insomnia.js
  // Keep screen on for our app
  // $cordovaInsomnia.keepAwake();


  //document.addEventListener("pause", onPause, false);
  //document.addEventListener("resume", onResume, false);

  /* var model =  $cordovaDevice.getModel();
  var platform =  $cordovaDevice.getPlatform();
  var version = $cordovaDevice.getVersion(); */


  //}//End of onDeviceReady() 

  /*  onPause() {
     log.debug('onPause About to call BGGS.start()');
     BackgroundGeolocationService.start();
 
     $rootScope.$broadcast('PAUSE');
   }
 
   onResume() {
     log.debug('onresume: about to broadcast: RESUME event');
     BackgroundGeolocationService.stop();
 
     $rootScope.$broadcast('RESUME');
   }
  */
  /*
   * For future use - maybe write to log file on device that can be send to OSX
   * or maybe viewed on a tab on the device
   
  function writeLog(str) {
    if(!logOb) return;
    var log = str + " [" + (new Date()) + "]\n";
    this.log.info("going to log "+log);
    logOb.createWriter(function(fileWriter) {
   
      fileWriter.seek(fileWriter.length);
   
      var blob = new Blob([log], {type:'text/plain'});
      fileWriter.write(blob);
      this.log.info("ok, in theory i worked");
    }, fail);
  }
  */


}



