import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';

import { LoopBackConfig } from '../../../shared/sdk/index';

import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { GpsAuditServiceProvider } from '../../providers/gps-audit-service/gps-audit-service'
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

/**
 * Generated class for the GpsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
interface Coordinates {
  latitude: number;
  longitude: number;
  accuracy: number;
  altitude: number;
  altitudeAccuracy: number;
  heading: number;
  speed: number;
}

interface Geoposition {
  coords: Coordinates;
  timestamp: number;
}

@IonicPage()
@Component({
  selector: 'page-gps',
  templateUrl: 'gps.html',
})
export class GpsPage {
  username: any;
  psIsWorking: boolean = false;
  posOptions = {
    timeout: 10000,
    enableHighAccuracy: false
  };
  error;
  debug;
  gpshistory;
  gpsIsWorking = false;
  gpsErrorMess;
  err;
  driverId: number;
  loginFlag = this.pdaParams.isDrvLoggedOn();

  position: Geoposition = {
    coords: {
      latitude: 0,
      longitude: 0,
      accuracy: 0,
      altitude: 100,
      altitudeAccuracy: 100,
      heading: 50,
      speed: 100,
    },
    timestamp: 10
  };
  success;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private pdaParams: PdaparamsServiceProvider, private configService: ConfigServiceProvider,
    private gpsAudit: GpsAuditServiceProvider, private geolocation: Geolocation,
    private sharedService: SharedServiceProvider) {
    this.username = navParams.data;
    //console.log("GpsPage loading...");
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
  }

  ionViewWillEnter() {
    this.driverId = this.pdaParams.getDriverId();
    this.error = "";
    this.debug = this.pdaParams.debugMode;
    this.gpshistory = this.gpsAudit.getHistory();

    //Riyaz: Using ionic native's geolocation plugin here
    this.geolocation.getCurrentPosition(this.posOptions).then((position) => {
      this.position.coords.latitude = position.coords.latitude;
      this.position.coords.longitude = position.coords.longitude;
      this.position.coords.altitude = position.coords.altitude;
      this.position.coords.accuracy = position.coords.accuracy;
      this.position.coords.altitudeAccuracy = position.coords.altitudeAccuracy;
      this.position.coords.heading = position.coords.heading;
      this.position.coords.speed = position.coords.speed;
      this.position.timestamp = position.timestamp;
      this.success = 'YES';
      this.gpsIsWorking = true;

    }).catch((err) => {
      this.success = 'FAIL';
      this.gpsIsWorking = false;
      this.gpsErrorMess = err.message;
      this.error = "Failed";
      this.err = err;
    });

  }

  getGPSIsworking = () => {
    return this.gpsIsWorking;
  }

  showResult = () => {
    return this.error == "";
  }

}



