import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'displayStatusDetail'})
export class DisplayStatusDetailPipe implements PipeTransform
{
    transform(input:any, args:string[]): any {
        input = input || '';
        var out = "";
        if (input == "IP")
            return "IN PROGRESS";
        if (input == "DN")
            return "PICKED UP";

        return "ALLOCATED";
    }
}


