import { Injectable, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';


import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';

/*
  Generated class for the ImageFileServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.

   // see: https://devdactic.com/complete-image-guide-ionic/
*/
@Injectable()
export class ImageFileServiceProvider {
  logParams: any;
  log: any;
  images = [];
  IMAGE_STORAGE_KEY = 'images';
  storeImage: any;
  removeImage: any;
  set: any;
  get: any;
  image: { name: string, uploaded: boolean, notes: string, readyForUpload: string };

  constructor(private pdaParams: PdaparamsServiceProvider, public http: Http,
    private logger: LogServiceProvider) {
    //console.log('Hello ImageFileServiceProvider Provider');

    this.logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'ImageFileServiceProvider' };
    this.log = this.logger.getInstance(this.logParams);

    this.storeImage = this.addImage;
    this.removeImage = this.deleteImage;
    this.set = this.setImageProperty;
    this.get = this.getImageProperty;
    this.images = this.getImages();
  }

  // Gets a list of image names from local storage - not the file system
  getImages() {   
     var img = localStorage.getItem(this.IMAGE_STORAGE_KEY);
      if (img) {
        this.images = JSON.parse(img);
      } else {
        this.images = [];
      }
 
    return this.images;
  }


  addImage = (imageData) => {
    this.images.push(imageData);   
    localStorage.setItem(this.IMAGE_STORAGE_KEY, JSON.stringify(this.images));
  };

  deleteImage = (imageId) => {
    this.images = this.getImages();
    var i = this.images.length;
    while (i--) {
      if (this.images[i].name === imageId) {
        this.images.splice(i, 1);       
        localStorage.setItem(this.IMAGE_STORAGE_KEY, JSON.stringify(this.images));
        break;
      }
    }
  };

  setImageProperty = (imageId, key, value) => {
    this.images = this.getImages();
    var i = this.images.length;
    while (i--) {
      if (this.images[i].name === imageId) {
        this.images[i][key] = value;
        localStorage.setItem(this.IMAGE_STORAGE_KEY, JSON.stringify(this.images));
        break;
      }
    }
  }

  getImageProperty = (imageId, key) => {
    this.images = this.getImages();
    var i = this.images.length;
    while (i--) {
      if (this.images[i].name === imageId) {
        return (this.images[i][key]);
      }
    }
    return null;		// didn't find what we were looking for
  };

  getImage = (imageId) => {
    this.images = this.getImages();
    var i = this.images.length;
    while (i--) {
      if (this.images[i].name === imageId) {
        return (this.images[i]);
      }
    }
    return null;		// didn't find what we were looking for
  };

}



