    WindowRefServiceProvider,
    SodServiceProvider,
    PushServiceProvider,
    ExitServiceProvider,
    GpsServiceProvider,
    Geolocation,
    ErrorModalServiceProvider,
    JobChangedServiceProvider,
    DeviceFeedback,
    ImageServiceProvider,
    ImageFileServiceProvider,
    File,
    Camera,
    Network,
    DriverMessageServiceProvider,
    BarcodeScanner,
    InAppBrowser,
    FileTransfer,
    FileTransferObject,
    ConnectivityMonitorProvider,
    SyncServiceProvider,
