import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

import { JobApi } from '../../../shared/sdk/services';
import { LoopBackConfig } from '../../../shared/sdk/index';

declare const lbclient: any;

import { FixedQueueServiceProvider } from '../fixed-queue-service/fixed-queue-service';
import { DeviceServiceProvider } from '../device-service/device-service';
import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { JobServiceProvider } from '../../providers/job-service/job-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { UtilServiceProvider } from '../util-service/util-service';

/*
  Generated class for the MessageServiceProvider provider.

  A Service to handle incoming PDA messages 

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
/*   interface payload {
    command:string,
    params:{}
   } */

@Injectable()
export class MessageServiceProvider {
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'MessageServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  /* Riyaz: fixedQueue is no longer imported as an npm library.
  It is now rewritten as an angular2 service within our codebase */
  msgQ = this.fixedQueue.create(50);// Store last x no. driver messages  	
  newMsgCount = 0;			// keep a count of new messages - will reset once driver has seen them
  deleteJobChangeData = lbclient.deleteJobChangeData;
  deleteJseaChangeData = lbclient.deleteJseaChangeData;

  constructor(private fixedQueue: FixedQueueServiceProvider,
    private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider,
    private deviceService: DeviceServiceProvider, private jobApi: JobApi,
    private jobService: JobServiceProvider,
    private sharedService: SharedServiceProvider,
    private utilService: UtilServiceProvider) {
    //console.log('Hello MessageServiceProvider Provider');
  }

  isJson(str) {
    try {
      JSON.parse(str);
    } catch (e) {
      return false;
    }
    return true;
  }

  dumpLocalStorage() {
    var logmsg = <any>{};
    var dblist = ['osc-local-db', 'osc-driver-info', 'osc-push-credentials'];
    var len = dblist.length;

    // dump ALL items, not just out predefined ones
    len = localStorage.length;

    for (var i = 0; i < len; i++) {
      var key = localStorage.key(i);
      var value = localStorage[key];

      logmsg.type = 'dumpLocalStorage:' + key;
      if (this.isJson(value))
        logmsg.data = JSON.parse(value);
      else
        logmsg.data = value;

      this.log.info(logmsg);
    }
  }

  clearLocalStorage() {
    var logmsg = <any>{};
    logmsg.type = 'clearLocalStorage';
    // clear local storage then log contents - should be empty
    localStorage.removeItem('osc-local-db');
    logmsg.data = JSON.parse(localStorage.getItem('osc-local-db'));
    this.log.info(logmsg);
  }

  clearChangeData() {
    var logmsg = <any>{};
    logmsg.type = 'clearChangeData';
    // clear job-change and checkpoint data from local storage then log contents
    this.deleteJobChangeData = (err, changeData) => {
      if (err)
        this.log.error(err);
      if (changeData)
        this.log.debug(changeData);
      logmsg.data = JSON.parse(localStorage.getItem('osc-local-db'));
      this.log.info(logmsg);
    }

    // clear jsea change data
    this.deleteJseaChangeData = (err, changeData) => {
      if (err)
        this.log.error(err);
      if (changeData)
        this.log.debug(changeData);
      logmsg.data = JSON.parse(localStorage.getItem('osc-local-db'));
      this.log.info(logmsg);
    }
  }

  dumpDeviceInfo() {
    var logmsg = <any>{};
    logmsg.type = 'dumpDeviceInfo';

    var deviceData = this.deviceService.getDevice();
    //var i = this.deviceService.getDevice()
    logmsg.data = JSON.stringify(deviceData);
    this.log.info(logmsg);
  }

  setPdaParam(params) {
    var logmsg = <any>{};
    logmsg.type = 'setPdaParam';
    this.pdaParams.setParameter(params, (err) => {
      if (err)
        this.log.error(err);
      this.log.info(logmsg);
    });
  }

  deleteJobs(params) {
    var logmsg = <any>{};
    logmsg.type = 'deleteJobs';

    var delfilter = params;
    this.log.info('filter:' + JSON.stringify(delfilter));

    // must supply where filter
    if (delfilter.where) {

      this.jobApi.find(delfilter, (err, jobs) => {
        var len = jobs.length;
        this.log.info("deleting:" + len + " legs");

        for (var leg = 0; leg < len; leg++) {
          var job = jobs[leg];
          this.log.info("delete leg:" + leg + " job:" + job.mobjobSeq);
          job.delete();
        }
      });
    }
    else {
      this.log.debug('No where filter provided');
    }
  }

  deleteOldJobs(params) {
    // REST API params object e.g.  { "daysback": 14 }
    var daysback = params.daysback || 7;		// default to 7 days
    this.jobService.deleteOldJobs(daysback);

  }

  startWatching() {

    // PDA commands 
    // From REST endpoint /TpmPdaControllers/sendCommandToPDA/{driver}
    this.sharedService.commandToPDA.subscribe(payload => {

      // $rootScope.$on('commandToPDA', function (event, payload) {

      this.log.debug('commandToPDA:received:' + JSON.stringify(payload));
      //console.log('commandToPDA:received 1:' + JSON.stringify(payload));

      switch (payload.command) {
        case 'dumpLocalStorage':
          this.dumpLocalStorage();
          break;

        case 'clearLocalStorage':
          this.clearLocalStorage();
          break;

        case 'clearChangeData':
          this.clearChangeData();
          break;

        case 'setPdaParam':
          this.setPdaParam(payload.params);
          break;

        case 'deleteJobs':
          this.deleteJobs(payload.params);
          break;

        case 'deleteOldJobs':
          this.deleteOldJobs(payload.params);
          break;

        default:
          var logmsg = <any>{};
          logmsg.type = 'commandToPDA';
          this.log.error('unknown command:' + payload.command);
          //console.log('unknown command:' + payload.command);
          break;
      }


      // LT - added after cordova/push upgrades - we didn't seem to get immediate updates afterwards
      //$rootScope.$apply();
    });

    // Message to driver
    //$rootScope.$on('messageToDriver', function (event, payload) {
    this.sharedService.messageToDriver.subscribe(payload => {

      //console.log('messageToDriver:received 1:' + JSON.stringify(payload));
      // payload.message has the message text

      //if (payload !== null) {
      if (!this.utilService.isEmpty(payload)) {
        // //console.log('messageToDriver:received 2:' + JSON.stringify(payload));
        this.msgQ.push(payload.message);
        this.newMsgCount++;
      }
      // LT - added after cordova/push upgrades - we didn't seem to get immediate updates afterwards
      //$rootScope.$apply();
    });

    // more watchers if required
  }

  getMessages() {
    // get all messages
    return (this.msgQ);
  }

  getNewMesssageCount() {
    return (this.newMsgCount);
  }

  resetNewMessageCount() {
    this.newMsgCount = 0;
  }

}



