import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'changeDisplayTo' })
export class ChangeDisplayToPipe implements PipeTransform {
    transform(input: any, job: any): any {
        if (job.displayTo)
            return job.displayTo;
        return input;
    }
}


