/* tslint:disable */
import { Injectable } from '@angular/core';
import { Job } from '../../models/Job';


import { TpmPdaController } from '../../models/TpmPdaController';
import { GpsHistory } from '../../models/GpsHistory';
import { Application } from '../../models/Application';
import { Installation } from '../../models/Installation';
import { Notification } from '../../models/Notification';
import { Push } from '../../models/Push';
import { Mobile_event } from '../../models/Mobile_event';
import { SiteConfig } from '../../models/SiteConfig';
import { DespatchToDriverMessages } from '../../models/DespatchToDriverMessages';
import { JseaDriverQuestions } from '../../models/JseaDriverQuestions';
import { JseaDriverAnswers } from '../../models/JseaDriverAnswers';

import { Photocontainer } from '../../models/Photocontainer';
import { Image } from '../../models/Image';
import { BarcodeHist } from '../../models/BarcodeHist';


export interface Models { [name: string]: any }

@Injectable()
export class SDKModels {

  private models: Models = {
    Job: Job,



    TpmPdaController: TpmPdaController,
    GpsHistory: GpsHistory,
    Application: Application,
    Installation: Installation,
    Notification: Notification,
    Push: Push,
    Mobile_event: Mobile_event,
    SiteConfig: SiteConfig,
    DespatchToDriverMessages: DespatchToDriverMessages,
    JseaDriverQuestions: JseaDriverQuestions,
    JseaDriverAnswers: JseaDriverAnswers,

    Photocontainer: Photocontainer,
    Image: Image,
    BarcodeHist: BarcodeHist


  };

  public get(modelName: string): any {
    return this.models[modelName];
  }

  public getAll(): Models {
    return this.models;
  }

  public getModelNames(): string[] {
    return Object.keys(this.models);
  }
}



