
import { Component } from '@angular/core';
import { Platform, AlertController, LoadingController, Loading } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { Observable } from 'rxjs/Rx';

import { LoopBackConfig } from '../../shared/sdk/index';
declare const lbclient: any;

import { BaseServiceProvider } from '../providers/base-service/base-service';
import { SiteconfigServiceProvider } from '../providers/siteconfig-service/siteconfig-service';
import { ConfigServiceProvider } from '../providers/config-service/config-service';
import { SharedServiceProvider } from '../providers/shared-service/shared-service';
import { SodServiceProvider } from '../providers/sod-service/sod-service';
import { CustomerconnServiceProvider } from '../providers/customerconn-service/customerconn-service'
import { PdaparamsServiceProvider } from '../providers/pdaparams-service/pdaparams-service';
import { ConnectivityMonitorProvider } from '../providers/connectivity-monitor/connectivity-monitor';
import { MessageServiceProvider } from '../providers/message-service/message-service';
import { JseaServiceProvider } from '../providers/jsea-service/jsea-service';
import { ImageServiceProvider } from '../providers/image-service/image-service';

import { LoginPage } from '../pages/login/login';
import { TabsPage } from '../pages/tabs/tabs';
import { PdaPage } from '../pages/pda/pda';

@Component({
  templateUrl: 'app.html'
})

export class MyApp {
  loading: Loading;
  allowed: boolean = false;
  connDetails;
  userId;
  result;
  Job = lbclient.models.LocalJob;
  rootPage: any;

  constructor(statusBar: StatusBar,
    private alertCtrl: AlertController, private loadingCtrl: LoadingController,
    splashScreen: SplashScreen, private siteConfig: SiteconfigServiceProvider,
    private configService: ConfigServiceProvider,
    private base: BaseServiceProvider, private sharedService: SharedServiceProvider,
    private customerConn: CustomerconnServiceProvider, private sodService: SodServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private platform: Platform,
    private connectivityMonitor: ConnectivityMonitorProvider,
    private messageService: MessageServiceProvider, private jseaService: JseaServiceProvider,
    private imageService: ImageServiceProvider) {

    //1.Is the user logged in?
    //2. No,the user is not logged in. Direct him to the login page.Set the firstime login variable.
    if (!(localStorage.getItem('login'))) {
      localStorage.setItem('firstlogin', 'true');
      this.rootPage = LoginPage;
      return;
    }

    //3. Is the Server API Url set?
    //4. No, the Server API Url is not set. Get & Set the the Server API Url.
    //Also set the server path for the loopback SDK builder in the ./shared/sdk directory.
    if (!(localStorage.getItem('apiURL'))) {
      this.userId = localStorage.getItem('userId');
      //console.log('userId====' + this.userId);
      this.connDetails = this.customerConn.getConnectionDetails(this.userId.toLowerCase());
      this.configService.setSiteDetails(this.connDetails);

      //Set the server path for the loopback SDK builder in the ./shared/sdk directory.
      LoopBackConfig.setBaseURL(this.connDetails.url);
      LoopBackConfig.setApiVersion('api');
    }

    //5.Check & set All Config data
    if (!(localStorage.getItem('SITE_CONFIG_LOADED'))) {
      console.log("Getting the siteconfigs!");
      var result$: Observable<string> = this.siteConfig
        .loadAllConfigsFromServer()
        .subscribe((result) => {
          console.log("Site Config Loaded!");
          this.CommonCode();
        },
          (error) => {
            this.showError("Unable to load Site Configs.Please check your connection.");
            return;
          });
    }
    else {
      this.CommonCode();
    }

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });

  }//constructor ends here

  //Shared code which should executes after siteconfigs are loaded.
  CommonCode = () => {
    //6. Check & set all table metadata
    //6.1 Load the jobs metadata  
    //console.log("Loading job metadata");
    var result: Observable<any> = this.base.getModelMetadata(this.Job, "jobMetadata");
    this.sharedService.jobMetaDataLoadedSubject.subscribe();

    //7. Check & set all watchers
    //7.1 Start monitoring network connectivity    
    this.connectivityMonitor.startWatching();

    //7.2 Start monitoring for messages   
    this.messageService.startWatching();

    //7.25 check if this is first start for the day
    this.sodService.checkDoSodAction('FIRST_RESUME');

    //7.3 Start monitoring for JSEA changes.
    this.jseaService.startWatching();

    //7.4  Start monitoring images for auto upload 
    this.imageService.startWatching();

    

    //console.log("pdaParams.getDriverId()==" + pdaParams.getDriverId());

    //8. If driverid > 0, redirect to jobs page
    if (this.pdaParams.getDriverId() > 0) {
      this.rootPage = TabsPage;
    }
    else {
      //9. If driverid <=0, redirect him to PDA page.
      this.rootPage = PdaPage;
    }
  }//CommonCode() ends here

  showError(text) {
    let alert = this.alertCtrl.create({
      title: 'Error',
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }


}



