import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Device } from '@ionic-native/device';
import { BackgroundGeolocation, BackgroundGeolocationConfig, BackgroundGeolocationResponse } from '@ionic-native/background-geolocation';

import { LoopBackConfig } from '../../../shared/sdk/index';
import { GpsHistory } from '../../../shared/sdk/models/GpsHistory';
import { gpshistoryapi } from '../../../shared/sdk/services/custom/GpsHistory';

import { GpsAuditServiceProvider } from '../gps-audit-service/gps-audit-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';

/*
  Generated class for the BackgroundGeolocationServiceProvider provider.
  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class BackgroundGeolocationServiceProvider {

  // GPS timestamps are in milliseconds since epoch - iOS also has a fractional part
  // e.g. iOS:		1463707063130.011
  // e.g. Android:	1463707069003
  // Site for millisecond calcs and conversions: http://currentmillis.com/

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'BackgroundGeolocationServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  lastGPSsecs = 0;		// last timestamp in seconds
  thisGPSsecs = 0;		// this timestamp in seconds
  diffGPSsecs = 0;
  threshold = 1;			// diff between gps must be greater than this to save
  ltimestamp: Date;
  platform: any;
  config: BackgroundGeolocationConfig = {
    desiredAccuracy: 10,
    stationaryRadius: 20,
    distanceFilter: 30,
    interval: 60000,// Android - The minimum time interval between location updates in milliseconds    
    //locationTimeout: 60,			
    activityType: 'AutomotiveNavigation',			// iOS hint
    debug: false,					// <-- enable this hear sounds for background-geolocation life-cycle
    stopOnTerminate: true			// <-- enable this to clear background location settings when the app terminates
  }

  constructor(private pdaParams: PdaparamsServiceProvider,
    private device: Device, private logger: LogServiceProvider,
    private gpsAudit: GpsAuditServiceProvider, private gpsHistApi: gpshistoryapi,
    private backgroundGeolocation: BackgroundGeolocation) {
    this.platform = this.device.platform;
    //console.log('Hello BackgroundGeolocationServiceProvider Provider');
  }


  saveGpsToDb = (drvid, location) => {
    //GpsHistory() is a call to the GpsHistory model
    var lgps = new GpsHistory();
    lgps.gps_driver_id = drvid;
    //var platform = $cordovaDevice.getPlatform();
    this.platform = this.device.platform;
    this.log.info("BGGS platform:" + this.platform + ", location:" + JSON.stringify(location));
    // LT - 06/07/2016 - with 2.52 release iOS devices do not appear to have a location.timestamp property
    // but do have the location.time property - check for both
    if (this.platform == 'ios') {
      if (location.timestamp) {
        lgps.gps_timestamp = location.timestamp;
      }
      else if (location.time) {
        lgps.gps_timestamp = location.time;
      }
      else {
        // lgps.gps_timestamp = Date.now();
        lgps.gps_timestamp = new Date();
        this.log.error("BGGS: no time provided so use now:" + lgps.gps_timestamp);
      }
      lgps.gps_heading = 0; // IOS Has a heading if u want to use it later
    }
    else {
      lgps.gps_timestamp = location.time;
      lgps.gps_heading = 0; // Android has a bearing if u want to use it later
    }


    this.ltimestamp = new Date(lgps.gps_timestamp);
    // check last GPS and only update if diff is greater than x seconds
    this.thisGPSsecs = this.ltimestamp.getTime() / 1000;				// current GPS time in seconds
    this.diffGPSsecs = this.thisGPSsecs - this.lastGPSsecs;
    this.log.info("BGGS: platform:" + this.platform + ", gpstimestamp:" + this.ltimestamp + ", lastGPSsecs:" + this.lastGPSsecs + ", thisGPSsecs:"
      + this.thisGPSsecs + ", diff:" + this.diffGPSsecs + ", threshold:" + this.threshold);
    this.lastGPSsecs = this.ltimestamp.getTime() / 1000;

    // lgps.gps_timestamp is UTC at this point
    var ldate = new Date(lgps.gps_timestamp);
    var oset = ldate.getTimezoneOffset();

    // lgps.gps_timestamp converted to local time - no timezone now
    var temp: any = this.ltimestamp.getTime() + (oset * -1) * 60 * 1000;
    lgps.gps_timestamp = temp;
    lgps.gps_latitude = location.latitude.toFixed(6);
    lgps.gps_longitude = location.longitude.toFixed(6);
    lgps.gps_quality = location.accuracy;
    lgps.gps_speed = 0; //location.coords.speed;
    lgps.gps_time = 0; //new Date(location.timestamp).getTime();

    // TODO - need to NOT rely on log - need our own access to this function
    var loggedOn = this.log.pdaParams.isDrvLoggedOn();
    this.log.info("BGGS driver:" + lgps.gps_driver_id + ", loggedOn:" + loggedOn + ", lgps:" + JSON.stringify(lgps));
    this.gpsAudit.saveGps(lgps);

    // TODO - do we need any more criteria to create history record?  if connected?
    if (lgps.gps_driver_id > 0 && this.diffGPSsecs > this.threshold && loggedOn) {
      this.log.info("About to save GPS for driver:" + lgps.gps_driver_id + ", lgps:" + JSON.stringify(lgps));
      this.gpsHistApi.create(lgps).subscribe(
        obj => {
          this.log.info("gpsHistApi.create success: obj:" + JSON.stringify(obj));
        },
        err => {
          this.log.error("gpsHistApi.create failed: err:" + JSON.stringify(err));
        }
      );
    }
  }//saveGpsToDb() ends here

  /*=====================================================*/
  /*             CALLBACK FROM PLUGIN                    */
  /*=====================================================*/
  callbackFn = (location) => {
    this.log.info('BGGS callbackFn: calling saveGpsToDb: location:' + JSON.stringify(location));
    this.saveGpsToDb(this.pdaParams.getDriverId(), location);

    // IMPORTANT:  We must execute the finish method here to inform the native plugin that we're finished,
    // and the background-task may be completed.  we must do this regardless if our HTTP request is successful or not.
    // IF we DON'T, ios will CRASH our APP for spending too much time in the background.
    if (this.platform == 'ios')
      this.backgroundGeolocation.finish();
  }

  failureFn = (error) => {
    this.log.info('BGGS failureFn: error:' + JSON.stringify(error));
  }

  /*=====================================================*/
  /*             START METHOD                            */
  /*=====================================================*/
  start = () => {
    this.backgroundGeolocation.configure(this.config)
      .subscribe((location: BackgroundGeolocationResponse) => {
        this.callbackFn(location);
      });
    this.log.info('BGGS START: configure with:' + JSON.stringify(this.config));
    // start recording location
    this.backgroundGeolocation.start();
  }

  /*=====================================================*/
  /*             STOP METHOD                            */
  /*=====================================================*/
  stop = () => {
    // If we wish to turn OFF background-tracking, we can call the #stop method.
    this.log.info('BGGS STOP Method called:');
    this.backgroundGeolocation.stop();
  }
}




