import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';

import 'rxjs/add/operator/takeUntil';
import { Subject } from 'rxjs/Subject';

import { LoopBackConfig } from '../../../shared/sdk/index';

import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { ConfigServiceProvider } from '../../providers/config-service/config-service';

import { HomePage } from '../home/home';
import { JobDetailsPage } from '../job-details/job-details';
import { DevicePage } from '../device/device';
import { GpsPage } from '../gps/gps';
import { PdaPage } from '../pda/pda';
import { JseaPage } from '../jsea/jsea';
import { MsgsPage } from '../msgs/msgs';
import { ImagesPage } from '../images/images';
import { BarcodePage } from '../barcode/barcode';
import { ChangePage } from '../change/change';



@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html'
})

export class TabsPage {
  private ngUnsubscribe: Subject<void> = new Subject<void>();
  tab1Root = HomePage;
  tab2Root = JobDetailsPage;
  tab3Root = DevicePage;
  tab4Root = GpsPage;
  tab5Root = PdaPage;
  tab6Root = JseaPage;
  tab7Root = MsgsPage;
  tab8Root = ImagesPage;
  tab9Root = BarcodePage;
  tab10Root = ChangePage;
  username = '';

  constructor(private nav: NavController, private auth: LoginServiceProvider, private configService: ConfigServiceProvider) {
    let info = this.auth.getUserInfo();
   
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
  }

  public logout() {
    this.auth.logout().takeUntil(this.ngUnsubscribe).subscribe(success => {
      this.nav.setRoot('LoginPage');
    });
  }
  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}



