import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'changeDisplaySvc' })
export class ChangeDisplaySvcPipe implements PipeTransform {
    transform(input: any, pdaParams: any): any {
        var output = "(" + input + ")";
        if (pdaParams.siteId === 'WADS')
            return "";
        return output;
    }
}


