import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'changeDisplayFrom'})
export class ChangeDisplayFromPipe implements PipeTransform
{
    transform(input:any, job:any): any {
        if (job.displayFrom)
        return job.displayFrom;
    return input;
    }
}


