import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { JobReplicationServiceProvider } from '../job-replication-service/job-replication-service'

/*
  Generated class for the SyncServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class SyncServiceProvider {


  mystr;
  lcallingfunc;
  isSyncing: boolean = false;

  //sync = lbclient.sync(this.arg1,this.arg2);

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'PlatformReadyServiceProvider' };
  log = this.logger.getInstance(this.logParams);

  constructor(public http: Http, private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider,
    public jobReplicationService: JobReplicationServiceProvider) {
    //console.log('Hello SyncServiceProvider Provider');
  }


  hybridSync = function (callback, filter) {

    this.mystr = 'syncService: About to sync SyncInprogress = ' + this.isSyncing + " Called from (last value set =) [" + this.lcallingfunc + "]";
    this.log.debug(this.mystr);

    if (!this.isSyncing) {
      this.isSyncing = true;

      this.mystr = 'syncService: isSyncInprogress is off so turn it on before Replication sync call ';
      this.log.debug(this.mystr);
      this.log.debug("SYNC: START");

      //console.log("+++++++++++++++++++++Calling jobReplicationService.sync in sync-service.ts.+++++++++++");
     
      this.jobReplicationService.sync(callback, filter);
    }
  }

  getSyncInProgress = function () {

    this.mystr = 'syncService: getSyncInProgress:' + this.isSyncing;
    this.log.debug(this.mystr);

    return this.isSyncing;
  };

  setCallingFunc = function (callingfunc) {

    this.mystr = 'syncService: In setCallingFunc value = ' + callingfunc;
    this.log.debug(this.mystr);

    this.lcallingfunc = callingfunc;
  };

  setSyncInProgress = function (torf) {

    this.isSyncing = torf;
    this.mystr = 'syncService: In setSyncInprogress setting value = ' + this.isSyncing;
    this.log.debug(this.mystr);
  };



}



