import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

declare const lbclient: any;

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';




/*
  Generated class for the JobServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class JobServiceProvider {
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'JobServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  jobs = [];
  Job = lbclient.models.LocalJob;
  job;
  constructor(private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider,
    private siteConfig: SiteconfigServiceProvider) {
    //console.log('Hello JobServiceProvider Provider');
  }

  deleteOldJobs(daysback) {
    // delete old jobs
    // if daysback parameter provided use it otherwise use site config value
    var val = this.siteConfig.getSiteConfigInt('PDA_DEL_DAYSBACK');//then(function (val) {

    daysback = daysback || val;

    var queryDate = new Date();
    queryDate.setDate(queryDate.getDate() - daysback);

    this.log.info("deleteOldJobs: daysback:" + daysback + ", queryDate:" + queryDate);
    //console.log("deleteOldJobs: daysback:" + daysback + ", queryDate:" + queryDate);
    daysback = 2;
    if (daysback > 1) {
      var delfilter = { "where": { "mobjobBookingDay": { "lt": queryDate } } };
      this.log.info("deleteOldJobs: delfilter:" + JSON.stringify(delfilter));
      this.Job.find(delfilter, (err, jobs) => {
        this.jobs = jobs;
        var len = this.jobs.length;
        this.log.info("deleteOldJobs: deleting:" + len + " job legs");
        //console.log("deleteOldJobs: deleting:" + len + " job legs");

        for (var leg = 0; leg < len; leg++) {

          this.job = this.jobs[leg];
          this.log.info("deleteOldJobs: delete leg:" + leg + " job:" + this.job.mobjobSeq);        

          //Riyaz: Deletes jobs from the local storage. 
          this.job.delete();



        }
      });
    }

  }



}



