import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { SignaturePad } from 'angular2-signaturepad/signature-pad';

import { LoopBackConfig } from '../../../shared/sdk/index';

import { ConfigServiceProvider } from '../../providers/config-service/config-service';

import { JobDetailsPage } from '../job-details/job-details';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

/**
 * Generated class for the SignaturePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 * 
 * See: http://www.9lessons.info/2017/04/ionic-3-and-angular-4-working-with.html
 */

@Component({
  selector: 'page-signature',
  templateUrl: 'signature.html',
})
export class SignaturePage {
  @ViewChild(SignaturePad) public signaturePad: SignaturePad;
  signature;
  podname;
  loginFlag = this.pdaParams.isDrvLoggedOn();
  signaturePadModel = {
    'podname': '',
    'signatureConfirm': false
  };

  public signaturePadOptions: Object = {
    /*  'minWidth': 2,
     'canvasWidth': 340,
     'canvasHeight': 200 */
    'backgroundColor': '#FFF',
    'minWidth': 1,
    'maxWidth': 1.5,
    'dotSize': 3,
    'penColor': 'rgb(66, 133, 244)',
    'onEnd': function () {
      //this.signature.image = this.signaturePad.toDataURL();
      this.signature = this.signaturePad.toDataURL();
    }
  };
  public signatureImage: string;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private configService: ConfigServiceProvider, public viewCtrl: ViewController,
    private pdaParams: PdaparamsServiceProvider, private sharedService: SharedServiceProvider) {
    //console.log("SignaturePage loading...");

    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
  }

  ionViewDidLoad() {
    //console.log('ionViewDidLoad SignaturePage');
    this.signature = localStorage.getItem("signature");
    if (this.signature)
      this.signaturePad.fromDataURL(this.signature);
    this.signaturePadModel.podname = "";
  }


  //Other Functions
  drawCancel() {
    this.signaturePadModel.signatureConfirm = false;
    this.signaturePadModel.podname = "";
    this.signaturePad.clear();
    this.signature = null;
    this.navCtrl.pop();
  }

  save() {
    this.signature = this.signaturePad.toDataURL();
    localStorage.setItem("signature", this.signature);
    this.podname = this.signaturePadModel.podname;
    this.signaturePadModel.podname = '';
    this.signaturePadModel.signatureConfirm = false;
    this.viewCtrl.dismiss({
      podname: this.podname,
      signature: this.signature
    });
  }

  clear() {
    this.signaturePadModel.signatureConfirm = false;
    this.signaturePad.clear();
    this.signature = null;
    localStorage.removeItem("signature");
    this.signaturePadModel.podname = "";
  }

  canvasResize() {
    let canvas = document.querySelector('canvas');
    this.signaturePad.set('minWidth', 1);
    this.signaturePad.set('canvasWidth', canvas.offsetWidth);
    this.signaturePad.set('canvasHeight', canvas.offsetHeight);
  }

  disableAcceptButton() {
    this.signature = this.signaturePad.toDataURL();
    return !(this.signaturePadModel.signatureConfirm && this.signature && this.signaturePadModel.podname)
  }
}



