import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Tab, Tabs, App } from 'ionic-angular';

import { Observable } from 'rxjs/Rx';

import { InAppBrowser } from '@ionic-native/in-app-browser';

import { LoopBackConfig } from '../../../shared/sdk/index';
import { TpmPdaControllerApi } from '../../../shared/sdk/services';

import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { LogServiceProvider } from '../../providers/log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';
import { BaseServiceProvider } from '../../providers/base-service/base-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { MessageServiceProvider } from '../../providers/message-service/message-service';
import { ExitServiceProvider } from '../../providers/exit-service/exit-service';
import { PushServiceProvider } from '../../providers/push-service/push-service';
import { EventServiceProvider } from '../../providers/event-service/event-service';


import { TabsPage } from '../tabs/tabs';
import { LoginPage } from '../login/login';
import { MyApp } from '../../app/app.component';

declare const lbclient: any;
/*
 * Generated class for the PdaPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-pda',
  templateUrl: 'pda.html',
})
export class PdaPage {
  t: Tabs = this.navCtrl.parent;
  clientlog = lbclient.clientlog;
  username: any;
  filter;
  result;
  driverId;
  userId;
  firstlogin;
  login;
  driverInfo;
  loginFlag = this.pdaParams.isDrvLoggedOn();
  TpmPdaControllers = [];
  TpmRowCount = 0;
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'PdaPage' };
  log = this.logger.getInstance(this.logParams);
  _filter = {
    "where": { and: [{ "tpmpc_driver_id": this.pdaParams.driverId, "tpmpc_access_allowed": 'Y', "tpmpc_access_registered": 'Y' }] }
  };
  _newfilter = {
    "where": { and: [{ "tpmpc_driver_id": this.pdaParams.driverId, "tpmpc_access_allowed": 'Y', "tpmpc_access_registered": { neq: "Y" } }] }
  };


  constructor(public navCtrl: NavController, public navParams: NavParams,
    private configService: ConfigServiceProvider, private pdaParams: PdaparamsServiceProvider,
    private siteConfig: SiteconfigServiceProvider, private logger: LogServiceProvider,
    private base: BaseServiceProvider, private sharedService: SharedServiceProvider,
    private TpmPdaController: TpmPdaControllerApi, private exitService: ExitServiceProvider,
    private messageService: MessageServiceProvider, private alertCtrl: AlertController,
    private pushService: PushServiceProvider, private iab: InAppBrowser,
    private eventService: EventServiceProvider, private app: App) {
    this.username = navParams.data;
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
    this.driverId = this.pdaParams.driverId;
    if (typeof (this.sharedService.loginFlag) == 'undefined')
      this.sharedService.loginFlag = this.pdaParams.isDrvLoggedOn();
    this.firstlogin = localStorage.getItem('firstlogin');
    this.result = this.getTpmPdaControllers(this._filter);
  }

  openPrivacyUrl(url) {
    this.iab.create(url, "_self");
  }

  getTpmPdaControllers = (filter) => {
    this.TpmPdaController.find(filter).subscribe((results) => {
      this.TpmPdaControllers = results;
      this.TpmRowCount = results.length;
      // If no rows returned reset driver no entered by user
      if (results.length == 0) {
        this.pdaParams.setDriverInfo(0);
        var prevId = this.pdaParams.getParams().driverId;
        //Create alert        
        if (this.driverId != prevId) {
          let alert = this.alertCtrl.create({
            title: 'Driver',
            message: 'Driver ID is not set. Please call support and then retry!',
            buttons: [
              {
                text: 'OK'
              }
            ]
          });
          alert.present();
        }
      }

      // TODO - need to ensure we have updated scope so in promise somewhere in TpmPdaController.find ??
      var TpmPdaControllerRow = this.TpmPdaControllers[0];		// should only ever be one
      if (TpmPdaControllerRow) {
        if (TpmPdaControllerRow.tpmpc_access_registered == 'N') {		// not yet registered
          TpmPdaControllerRow.tpmpc_access_registered = 'Y';
          TpmPdaControllerRow.tpmpc_last_update_time = new Date().toISOString();
          this.TpmPdaController.upsert(TpmPdaControllerRow).subscribe();
          this.sharedService.tpmPdaCtlsLoadedSubject.next(true);
        }
      }
    });
    return 1;
  };


  updateDriverPrompt = () => {
    let alert = this.alertCtrl.create({
      title: 'Driver',
      message: 'Enter the driver number to update',
      inputs: [
        {
          name: 'drivernum',
          placeholder: 'Driver ID'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            console.log('Cancel clicked');
            return;
          }
        },
        {
          text: 'Update',
          handler: (data) => {
            this.updateDriver(data.drivernum);

          }
        }
      ]
    });
    alert.present();
  }// updateDriverPrompt() ends here

  updateDriver = (driverId) => {
    //Create alert        
    if (isNaN(driverId)) {
      let alert = this.alertCtrl.create({
        title: 'Driver',
        message: 'Please enter correct Driver ID.',
        buttons: [
          {
            text: 'OK'
          }
        ]
      });
      alert.present();
      return;
    }
    this.driverId = driverId;

    // TODO - we need to unregister from push services and reregister with new driver id
    // reset _newfilter as driver id has changed
    var _newfilter = {
      "where": { and: [{ "tpmpc_driver_id": this.driverId, "tpmpc_access_allowed": 'Y', "tpmpc_access_registered": { neq: "Y" } }] }
    };
    this.filter = Object.assign({}, _newfilter);
    this.getTpmPdaControllers(this.filter);

    //Driver number has changed - normally from default of 0 to real driver id when initialising
    //Get rid of all stuff related to the driver so we dont get wrong data.
    if (this.firstlogin === 'false') {
      localStorage.clear();
      this.pdaParams.clearDriverInfo();
    }

    this.clientlog.info("About to call getTpmPdaControllers with filter:" + JSON.stringify(this.filter));
    this.log.info("About to call getTpmPdaControllers with filter:" + JSON.stringify(this.filter));
    // Send LOGON event
    //LOGON is done via a button now
    if (this.driverId > 0 && this.firstlogin === 'false') {
      this.eventService.sendMsg('LOGON');
      // Dump local storage at logon time - debug aid
      this.messageService.dumpLocalStorage();
    }
    this.pdaParams.setDriverInfo(this.driverId);
    this.pdaParams.logoffDriver();
    //If firsttime login, show the home page
    if (this.firstlogin === 'true') {
      this.navCtrl.setRoot(TabsPage);
    }
    else {
      this.app.getRootNav().setRoot(MyApp);
    }
  }//updateDriver() ends here


  pushDeRegister = function () {
    this.pushService.unRegister();
  }

  pushRegister = function () {
    this.pushService.registerForPush();
  }

  logon = function () {
    this.pdaParams.logonDriver();
    this.eventService.sendMsg('LOGON');
    // Dump local storage at logon time - debug aid
    this.messageService.dumpLocalStorage();
  }

  logoff = function () {
    this.pdaParams.logoffDriver();
    this.eventService.sendMsg('LOGOFF');
  }

}



