import { Component } from '@angular/core';
import { App, NavController, AlertController, LoadingController, Loading, IonicPage } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/takeUntil';
import { Observable } from 'rxjs/Rx';

import { LoopBackConfig } from '../../../shared/sdk/index';
import { JobApi } from '../../../shared/sdk/services';

import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { CustomerconnServiceProvider } from '../../providers/customerconn-service/customerconn-service'
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';
import { BaseServiceProvider } from '../../providers/base-service/base-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

import { TabsPage } from '../tabs/tabs';

/**
 * Generated class for the LoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  private ngUnsubscribe: Subject<void> = new Subject<void>();

  loading: Loading;
  registerCredentials = { username: '', password: '' };
  allowed: boolean = false;
  connDetails;
  //dataLoaded: boolean = false;
  rootPage: any;
  error;

  constructor(private app: App, private navCtrl: NavController, private loginService: LoginServiceProvider,
    private alertCtrl: AlertController, private loadingCtrl: LoadingController,
    private localStorage: Storage, private configService: ConfigServiceProvider,
    private siteConfig: SiteconfigServiceProvider, private base: BaseServiceProvider,
    private customerConn: CustomerconnServiceProvider,
    private sharedService: SharedServiceProvider) {
    //console.log("LoginPage loading...");

  }

  public login() {
    //this.showLoading();
    this.loginService.login(this.registerCredentials)
      .takeUntil(this.ngUnsubscribe)
      .subscribe(
        (allowed) => {
          this.allowed = allowed;
        },
        error => {
          this.showError(error);
        },
        () => {
          if (this.allowed) {
            // Retrieve current login details
            var loginDetails = JSON.parse(localStorage.getItem('login'));
            // If no login details assume new installation or reset
            if (!loginDetails) {
              // 1st time ever logged in on PDA
              loginDetails = {
                username: this.registerCredentials.username,
                time: new Date().toISOString()
              };
              // Store details in local storage           
              localStorage.setItem('login', JSON.stringify(loginDetails));
            }
            else {
              // update last login time
              loginDetails.time = new Date().toISOString();
              localStorage.setItem('login', JSON.stringify(loginDetails));
            }
            //Set the clientid         
            localStorage.setItem('userId', this.registerCredentials.username);
            //Redirect to the root app component.
            document.location.href = 'index.html';
          } else {
            this.showError("Access Denied!");
          }
        });//login subscription ends here  

  }//login() ends here

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

  showError(text) {
    let alert = this.alertCtrl.create({
      title: 'Login failed!',
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}



