import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

import { LoopBackConfig } from '../../../shared/sdk/index';
import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

/**
 * Generated class for the JobDetailNotesPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-job-detail-notes',
  templateUrl: 'job-detail-notes.html',
})
export class JobDetailNotesPage {
  loginFlag = this.pdaParams.isDrvLoggedOn();
  note = {
    'seqid': '',
    'formtype': '',
    'text': '',
    'placeholderText': ''
  };

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private viewCtrl: ViewController, private configService: ConfigServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private sharedService: SharedServiceProvider) {
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
    this.note = Object.assign({}, this.navParams.get('note'));
  }


  clear() {
    this.note.formtype = '';
    this.note.placeholderText = '';
    this.note.text = '';
    this.note.seqid = '';
  };

  drawCancel() {
    this.clear();
    this.navCtrl.pop();
  }

  save() {
    var seqid = this.note.seqid;
    var noteText = this.note.text;
    this.note.text = "";
    this.viewCtrl.dismiss({
      noteText: noteText,
      seqid: seqid
    });
  };
}



