import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Tabs } from 'ionic-angular';

import { LoopBackConfig } from '../../../shared/sdk/index';
import { DespatchToDriverMessagesApi } from '../../../shared/sdk';

import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { LogServiceProvider } from '../../providers/log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';
import { BaseServiceProvider } from '../../providers/base-service/base-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { MessageServiceProvider } from '../../providers/message-service/message-service';
import { DriverMessageServiceProvider } from '../../providers/driver-message-service/driver-message-service';

/**
 * Generated class for the MsgsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-msgs',
  templateUrl: 'msgs.html',
})
export class MsgsPage {

  t: Tabs = this.navCtrl.parent;
  username: any;
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'MsgsPage' };
  log = this.logger.getInstance(this.logParams);
  messageText;
  boxShow = false;
  textareadata;
  messages = [];
  driverId;
  err;
  filter;
  loginFlag = this.pdaParams.isDrvLoggedOn();

  constructor(public navCtrl: NavController, public navParams: NavParams, private configService: ConfigServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private alertCtrl: AlertController, private siteConfig: SiteconfigServiceProvider,
    private logger: LogServiceProvider, private base: BaseServiceProvider, private sharedService: SharedServiceProvider,
    private messageService: MessageServiceProvider, private driverMessageService: DriverMessageServiceProvider,
    private despatchToDriverMessages: DespatchToDriverMessagesApi) {
    this.username = navParams.data;
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
    this.driverId = this.pdaParams.getDriverId();
    this.filter = {
      "where": { "drvmDriverId": this.driverId },
      "order": 'drvmSeqid DESC',
      "limit": 20
    };
  }
  ionViewWillEnter() {
    this.getMessages();
    // Reset new message count now they have been viewed
    this.messageService.resetNewMessageCount();
  }


  getMessages() {
    this.despatchToDriverMessages.find(this.filter).subscribe(
      (msgs) => {
        this.messages = msgs;
        //console.log("messages from inside subscribe:::" + this.messages);
      },
      err => {
        this.err = err;
      }
    )
  }

  // Reshow messages as required from callbacks etc.
  showMessages(err) {
    this.messages = this.err;
  }

  showMessageEntryBox(tf) {
    this.boxShow = tf;
  }

  newMessage() {
    this.textareadata = "Input your text here";
    this.showMessageEntryBox(true);
  }

  onSubmit() {
    this.messageText = this.textareadata;
    this.driverMessageService.addMessage(this.pdaParams.getDriverId(), this.messageText, this.showMessages);
    this.showMessageEntryBox(false);
    this.getMessages();
  }

  deleteAllMessages() {
    let confirmPopup = this.alertCtrl.create({
      title: 'DELETE All messages',
      subTitle: 'Are you sure you want to DELETE All messages?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            //console.log('User has DELETED all messages');
            this.log.debug('User has DELETED all messages');
            return false;
          }
        },
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            //console.log('Cancel clicked');
            this.log.debug('User has CANCELLED out of DELETE All messages');
            return;
          }
        }
      ]
    });
    confirmPopup.present();
  }//End of  deleteAllMessages() messages

  quit() {
    this.showMessageEntryBox(false);
    this.t.select(0);
  }
}



