import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';
import { SharedServiceProvider } from '../shared-service/shared-service';

declare const lbclient: any;
/*
  Generated class for the BarcodeServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class BarcodeServiceProvider {
  //When App starts Get from localstorage
  syncrequired: any = localStorage.getItem('BARCODES_SYNC_REQD');
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'BackgroundGeolocationServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  apponline: any = true;
  barcode_sync = lbclient.barcode_sync;

  constructor(public http: Http, private pdaParams: PdaparamsServiceProvider,
    private logger: LogServiceProvider, private sharedService: SharedServiceProvider) {
    //console.log('Hello BarcodeServiceProvider Provider');
    this.syncrequired = (this.syncrequired == "true");
    this.sharedService.APP_ON_LINE.subscribe(event => {
      this.syncrequired = localStorage.getItem('BARCODES_SYNC_REQD');
      this.syncrequired = (this.syncrequired == "true");
      this.apponline = localStorage.getItem('APP_CONECTIVITY');
      this.apponline = (this.apponline == "true");
      this.log.debug(" APP_ON_LINE: Syncrequired = " + this.syncrequired + " AppOnline From LocaStorage = " + this.apponline);

      //If the last state from localstorage was offline the sync if required
      if (this.syncrequired && this.apponline == false) {
        this.log.debug(" SYNC Required and apponline from LStorage is FALSE so will barcode_sync:" + event);
        this.barcode_sync();
        localStorage.setItem('BARCODES_SYNC_REQD', 'false');
      }

      //Now Update Local Storage
      this.apponline = true;
      localStorage.setItem('APP_CONECTIVITY', this.apponline);
    });
    this.sharedService.APP_OFF_LINE.subscribe(event => {
      this.apponline = false;
      localStorage.setItem('APP_CONECTIVITY', this.apponline);
    });

  }//End of the constructor.

  //This is called from BarcodeCtrl and will update Local Storage
  setSyncRequired = (val) => {
    localStorage.setItem('BARCODES_SYNC_REQD', val);
    this.log.debug(" setSyncRequired:" + val);
    this.syncrequired = val;
  }

}



