import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

/*
  Generated class for the LoginServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.

  See also https://devdactic.com/login-ionic-2/
*/

export class User {
  username: string;

  constructor(username: string) {
    this.username = username;
  }
}

@Injectable()
export class LoginServiceProvider {
  currentUser: User;

  constructor() {
    //console.log('Hello LoginServiceProvider Provider');
  }


  public login(credentials) {
    //Empty credential check
    if (credentials.username === null || credentials.password === null) {
      return Observable.throw("Please insert credentials");
    }
    else //Run a credential check
    {
      //console.log("Logged in with user :"+credentials.username);
      /* A request to the loopback backend could be made here, to check
       the credentials. But for now, we are using the following hardcoded logic. */
      let access = false;

      if (credentials.username == 'oscvtest' && credentials.password == 'oscvtest') {
        access = true;
      } else if (credentials.username == 'scope' && credentials.password == 'scope') {
        access = true;
      } else if (credentials.username == 'swift1' && credentials.password == 'swift1') {
        access = true;
      } else if (credentials.username == 'wpe' && credentials.password == 'wpe') {
        access = true;
      } else if (credentials.username == 'aptold' && credentials.password == 'aptold') {
        access = true;
      } else if (credentials.username == 'apt' && credentials.password == 'apt') {
        access = true;
      } else if (credentials.username == 'ces' && credentials.password == 'ces') {
        access = true;
      } else if (credentials.username == 'redback' && credentials.password == 'redback') {
        access = true;
      } else if (credentials.username == 'swift2' && credentials.password == 'swift2') {
        access = true;
      } else if (credentials.username == 'cct' && credentials.password == 'cct') {				// City Crane
        access = true;
      } else if (credentials.username == 'itd' && credentials.password == 'itd') {				// Intime
        access = true;
      } else if (credentials.username == 'glc' && credentials.password == 'glc') {				// Greenlight
        access = true;
      } else if (credentials.username == 'ace' && credentials.password == 'ace') {				// Ace
        access = true;
      } else if (credentials.username == 'pam' && credentials.password == 'pam') {				// P and M
        access = true;
      } else if (credentials.username == 'flg' && credentials.password == 'flg') {				// Fair Logistics
        access = true;
      } else if (credentials.username == 'wads' && credentials.password == 'wads') {			// WADS
        access = true;
      } else if (credentials.username == 'univ' && credentials.password == 'univ') {			// Universal
        access = true;

      } else {
        return Observable.throw("Wrong credentials!");
      }


      return Observable.create(
        observer => {
          this.currentUser = new User(credentials.username);

          observer.next(access);
          observer.complete();
        });

    }
  }

  public getUserInfo(): User {
    return this.currentUser;
  }

  public logout() {
    return Observable.create(
      observer => {
        this.currentUser = null;
        observer.next(true);
        observer.complete();
      }
    );
  }

}


