import { Injectable } from '@angular/core';

import { AlertController } from 'ionic-angular';
import 'rxjs/add/operator/map';

import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';

/*
  Generated class for the ErrorModalServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class ErrorModalServiceProvider {

  constructor(private pdaParams: PdaparamsServiceProvider,
    private logger: LogServiceProvider, private alertCtrl: AlertController) {
    //console.log('Hello ErrorModalServiceProvider Provider');
  }

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'ErrorModalServiceProvider' };
  log = this.logger.getInstance(this.logParams);

  showModal(modalOptions) {
    var errortext = "Status:" + modalOptions.error.status + " url:" + modalOptions.error.config.url;

    let alert = this.alertCtrl.create({
      title: this.pdaParams.getAppVersion(),
      subTitle: modalOptions.headerText,
      message: errortext,
      buttons: ['Dismiss']
    });
    alert.present();

    this.log.error('Error:' + JSON.stringify(modalOptions.error));
  };//showModal ends here


}



