import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Tabs } from 'ionic-angular';

import { LoopBackConfig } from '../../../shared/sdk/index';
import { JseaDriverQuestionsApi } from '../../../shared/sdk/services/custom/JseaDriverQuestions';

import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { LogServiceProvider } from '../../providers/log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';
import { BaseServiceProvider } from '../../providers/base-service/base-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { JseaServiceProvider } from '../../providers/jsea-service/jsea-service';

import { TabsPage } from '../tabs/tabs';

import { LoginPage } from '../login/login';
declare const lbclient: any;

/**
 * Generated class for the JseaPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-jsea',
  templateUrl: 'jsea.html',
})
export class JseaPage {

  t: Tabs = this.navCtrl.parent;
  username: any;
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'JobDetailsPage' };
  log = this.logger.getInstance(this.logParams);
  pda_jsea_on;
  lpda_jsea_on;
  jseaQuestions = [];
  LocalJseaDriverAnswers = lbclient.models.LocalJseaDriverAnswers;
  jseaAnswers_sync = lbclient.jseaAnswers_sync;
  driverId: number;
  firstlogin;
  loginFlag = this.pdaParams.isDrvLoggedOn();

  constructor(public navCtrl: NavController, public navParams: NavParams, private configService: ConfigServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private siteConfig: SiteconfigServiceProvider,
    private logger: LogServiceProvider, private base: BaseServiceProvider, private sharedService: SharedServiceProvider,
    private jseaService: JseaServiceProvider, private alertCtrl: AlertController,
    private jseaDriverQuestionsApi: JseaDriverQuestionsApi) {
    this.username = navParams.data;
    //console.log("JseaPage loading...");
    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
  }

  ionViewDidEnter() {
    this.firstlogin = localStorage.getItem('firstlogin');
    this.driverId = this.pdaParams.getDriverId();
    if (this.sharedService.testing) {
      this.pda_jsea_on = 'Y';
      this.lpda_jsea_on = 'Y';
    }
    else {
      this.pda_jsea_on = this.siteConfig.getSiteConfigValue('PDA_JSEA_ON');
      this.lpda_jsea_on = this.pdaParams.pda_jsea_on ? 'Y' : 'N';
    }

    if (this.pda_jsea_on != 'Y' && this.lpda_jsea_on != 'Y') {
      let alert = this.alertCtrl.create({
        title: 'Module not installed',
        subTitle: 'This module is not installed.',
        buttons: [{
          text: 'OK',
          handler: () => {
            const navTransition = alert.dismiss();
            //console.log('OK clicked');
            navTransition.then(() => {
              this.t.select(0);
            });
            return false;
          }
        }]
      });
      alert.present();
    }
    else {
      var formType = this.jseaService.getServiceFormType();
      var _allfilter = {
        "where": { "jdqType": formType },
        "order": "jdqOrder ASC"
      }
      var filter = Object.assign({}, _allfilter);
      this.jseaDriverQuestionsApi.find(filter).subscribe((questsarr) => {
        // TODO - need to check error and only show jobs if no error
        this.jseaQuestions = questsarr;
      });
    }//End of else
  }//End of ionViewDidEnter()


  checkJdqResponse = function (jdqCbox, jdqType, jdqOrder, jdqNewForm, jdqNewFormType) {
    // check response when items are changed in case we need to do
    // any additional work e.g. load some more questions

    if (jdqCbox) {				// box has been ticked
      if (jdqNewForm) {		// new form required
        // jdqNewFormType has the type we need
        var filter = {
          "where": { "jdqType": jdqNewFormType },
          "order": "jdqOrder ASC"
        };

        this.jseaDriverQuestionsApi.find(filter)
          .subscribe((questsarr) => {
            var len = questsarr.length;
            for (var i = 0; i < len; i++) {
              this.jseaQuestions.push(questsarr[i]);
            }
          });
      }
    }
    else {		// box unticked - might need to remove additional form data
      if (jdqNewForm) {		// new form required
        for (var i = this.jseaQuestions.length - 1; i >= 0; i--) {
          if (this.jseaQuestions[i].jdqType === jdqNewFormType) {
            this.jseaQuestions.splice(i, 1);
            // break;       //<-- Uncomment  if only the first term has to be removed
          }
        }
      }
    }
  }

  doSubmitWork = function () {
    var li;
    var llen;
    //They Have Submited a list of Ticks Y/N and we now have to create an array of answers
    // one row for each answer	
    var ljobnum = this.jseaService.getServiceJobNum();
    var ljobdate = this.jseaService.getServiceJobDate();
    var jobStatusType = this.jseaService.getServiceStatusType();
    var formLeg = this.jseaService.getServiceFormLeg();
    llen = this.jseaQuestions.length;

    for (li = 0; li < llen; li++) {
      var AnswerSession = new lbclient.models.LocalJseaDriverAnswers();
      AnswerSession.jdaSeqnum = 0;


      AnswerSession.jdaJobNum = ljobnum;
      AnswerSession.jdaJobBday = ljobdate;		// is a date object already 
      AnswerSession.jdaDriverId = this.pdaParams.getDriverId();
      AnswerSession.jdaFormType = this.jseaQuestions[li].jdqType;
      AnswerSession.jdaFormLeg = formLeg;
      AnswerSession.jdaOrder = this.jseaQuestions[li].jdqOrder;
      AnswerSession.jdaCheckBox = this.jseaQuestions[li].jdqCbox;
      AnswerSession.jdaJobStatusType = jobStatusType;
      AnswerSession.save();
    }
    this.jseaAnswers_sync();
  }


  /*========================================*/
  /*      JSEA SUBMIT   */
  /*========================================*/
  jseaSubmit = function () {
    let subconfirmPopup = this.alertCtrl.create({
      title: 'Jsea Session Confirm',
      message: 'Are you sure you want to submit your Answers session to the database ?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            const navTransition = subconfirmPopup.dismiss();
            this.log.debug('User has Submitted the Jsea session');
            this.jseaService.setJseaIsCaptured("Y");
            //Handle Answers
            this.doSubmitWork();
            navTransition.then(() => {
              //this.navCtrl.setRoot(TabsPage);
              this.t.select(0);
            });
            /*  navTransition.then(() => {
               if (this.firstlogin === 'true') {
                 localStorage.setItem('firstlogin', 'false');
                 this.navCtrl.setRoot(TabsPage);
               }
               else
                 this.t.select(0);
 
             }); */
            return false;
          }
        },
        {
          text: 'No',
          handler: () => {
            this.log.debug('User has CANCELLED out of a Jsea session SUBMIT Doing nothing');
            return;
          }
        }
      ]
    });
    subconfirmPopup.present();
  }

  /*========================================*/
  /*        SCAN SESSION CANCEL   */
  /*========================================*/
  jseaCancel = function () {
    let confirmPopup = this.alertCtrl.create({
      title: 'CANCEL Jsea Session',
      message: 'Are you sure you want to CANCEL this jsea session (You will lose all data )?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            const navTransition = confirmPopup.dismiss();
            this.log.debug('User has Cancelled the jsea session');
            navTransition.then(() => {
              //this.navCtrl.setRoot(TabsPage);
              this.t.select(0);
            });
            /* navTransition.then(() => {
              if (this.firstlogin === 'true') {
                this.navCtrl.push(LoginPage);
                //return;
              } else {
                this.t.select(0);
              }
            }); */
            return false;
          }
        },
        {
          text: 'No',
          handler: () => {
            this.log.debug('User has CANCELLED out of a Jsea session CANCEL');
            return;
          }
        }
      ]
    });
    confirmPopup.present();
  }
}



